<!-- eslint-disable vue/multi-word-component-names -->
<template>
  <div class="col-md-3 col-md-offset-1">
    <div class="rshang">
      <p class="lead">
        <i class="glyphicon glyphicon-sort-by-attributes-alt"></i>
        <strong>阅读资讯排行榜</strong>
      </p>
      <ul>
        <li v-for="(n, value) in hotlist1.slice(25, 34)" :key="value">
          <h4>
          <span style="font-size: 18px; float: left">{{ value + 1 }}</span>
          <div class="title">&nbsp;&nbsp;{{ n.title }}</div>
          <p class="text-right text-warning">
            <i class="glyphicon glyphicon-eye-open"></i>
            {{ n.readNum }}
          </p>
          </h4>
        </li>
      </ul>
    </div>
  </div>
</template>
  
<script>
export default {
  data() {
    return {
      url: "http://124.93.196.45:10001",
      hotlist: [],
    };
  },
  computed: {
    hotlist1: function () {
      return this.hotlist.sort(function (a, b) {
        var x = a["readNum"];
        var y = b["readNum"];
        return x > y ? -1 : x < y ? 1 : 0;
      });
    },
  },
  created() {
    this.axios({
      method: "get",
      url: " /prod-api/api/movie/press/press/list",
    })
      .then((resopnse) => {
        if (resopnse.status == 200) {
          if (resopnse.data.code == 200) {
            console.log(resopnse.data);
            this.hotlist = resopnse.data.rows;
            console.log(this.hotlist);
          } else {
          }
        }
      })
      .catch((error) => {
        console.log(error);
      });
  },
};
</script>
  
<style scoped>
.col-md-3{
  background: #fff;
}
.rshang ul li {
  margin-left: -50px;
  margin-top: 20px;
}
.title {
  width: 160px;
  float: left;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
</style>