<!-- eslint-disable vue/multi-word-component-names -->
<template>
  <div class="col-md-8 xia">
    <ul>
      <li v-for="n in list.slice(25, 30)" :key="n.id">
        <img :src="this.url + n.cover" alt="" />
        <div style="float: left; margin-left: 10px">
          <router-link :to="{ name: 'newsdetail', params: { id: n.id } }">
            <h4 style="margin-top: 0px"><strong>{{ n.title }}</strong></h4>
          </router-link>
          <div class="box" v-html="n.content"></div>
          <p class="text-muted">
            {{ n.publishDate }}
            <span style="margin-left: 280px" >
              <i class="glyphicon glyphicon-heart"></i>&nbsp;{{ n.likeNum}}
            </span>
            <span style="margin-left: 12px">
              <i class="glyphicon glyphicon-eye-open"></i>&nbsp;{{ n.readNum}}
            </span>
            <span style="margin-left: 18px">
              <i class="glyphicon glyphicon-edit"></i>&nbsp;{{ n.commentNum }}
            </span>
          </p>
        </div>
      </li>
    </ul>
  </div>
</template>
  
<script scoped>
export default {
  data() {
    return {
      url: "http://124.93.196.45:10001",
      list: [],
      totalPage: [], // 所有分页的数据
      pageSize: 5, // 每页显示数量
      pageNum: 1, // 共几页=所有数据/每页现实数量
      currentPage: 0, // 默认当前显示第一页
      listShow: [],
    };
  },
  created() {
    this.axios({
      method: "get",
      url: " /prod-api/api/movie/press/press/list",
    })
      .then((resopnse) => {
        if (resopnse.status == 200) {
          if (resopnse.data.code == 200) {
            console.log(resopnse.data);
            this.list = resopnse.data.rows;
            this.listShow = resopnse.data.rows;
            console.log(this.list);
          } else {
          }
        }
      })
      .catch((error) => {
        console.log(error);
      });
  },
};
</script>
  
<style scoped>
a{
  text-decoration: none;
  
}
.xia{
  background: #fff;
}
.xia p{
  margin-bottom: 20px;
}
ul{
  padding-left: 0px;
}
.xia img {
  width: 200px;
  height: 190px;
  float: left;
}
.xia ul li {
  list-style-type: none;
}
.box {
  float: left;
  width: 510px;
  height: 120px;
  padding: 0px;
  padding-right: 10px;
  margin-bottom: 20px;
  line-height: 32px;
  position: relative;
  text-overflow: ellipsis;
  overflow: hidden;
}
.box:after {
  content: "...";
  position: absolute;
  right: 0;
  bottom: 0;
}
</style>